import React from "react"
import { Link } from "gatsby"
import ReactTypingEffect from "react-typing-effect"

import Layout from "../components/layout"
import Image from "../components/image"
import SEO from "../components/seo"

const IndexPage = () => (
  <Layout>
    <SEO title="Home"/>
    <h1><ReactTypingEffect
      text="Hi, my name is Kevin!"
      speed={150}
      eraseDelay={10000}
      typingDelay={1000}
    /></h1>
    <p>
      I'm a <b>rising sophomore</b> at <b>Harvard University</b> planning to study <b>computer
      science</b> and <b>math</b>.
      I care about writing elegant code, simplifying designs, and telling the stories behind data.
      Outside of class, I'm technical director of <a href={"http://hodp.org/"}><b>Harvard Open Data Project</b></a>,
      a principle in <a href={"https://www.harvardcap.org/"}><b>Harvard Undergraduate Capital Partners</b></a>,
      and a member of <a href={"https://datamatch.me/"}><b>Datamatch</b></a>.
    </p>
    <div style={{ maxWidth: `300px`, marginBottom: `1.45rem` }}>
      <Image/>
    </div>
    <Link to="/page-2/">Go to page 2</Link>
  </Layout>
)

export default IndexPage
